<template>

    <div class="appBox">
        <div class="header">
            <img class="img1" src="@/static/images/modify/title_image.png" alt />
            <img class="img2" src="@/static/images/modify/treebg_iamge.png" alt />
        </div>
        <div class="activeRule">
            <p class="title">财富树模式</p>
            <div class="rule">
                <div class="ruTitle">投入小、回报大、不崩盘的社群新模式！</div>
                <p>财富树模式是一套没有泡沫、奖励机制巨大、投入小收益大的社群模式玩法，动态奖金是100%，而且永远不会崩盘，无后顾之忧。</p>
            </div>
            <p class="title">172倍收益，等您来拿！</p>
            <div class="rule">
                <p>举例来说，按照经典的双轨线、八层级来计算，创建者和参与者即可通过裂变的力量，赚取172倍的收益，而且模式永不崩盘，投入小产出大，社区接受程度高。</p>
            </div>
            <p class="title">用户福利</p>
            <div class="rule">
                <p>首次注册就送100元的MCT平台币！创建红包群再送100元的MCT平台币！注：MCT是millioncoin.name的平台币，未来MCT将会在millioncoin.name平台中起到空投糖果所得权、平台股份的持币分红权等应用，millioncoin.name也将给MCT更多赋能，让MCT有更多的价值落地。</p>
            </div>
            <div class="rule1 ov">
                <div class="fl treepwd">
                    <p>注册邀请码</p>
                    <span>{{inviteData.user_code}}</span>
                    <button class="copy" @click="copyInvite(inviteData.user_code)">复制</button>
                </div>
                <div class="fl treepwd">
                    <p>财富树密码</p>
                    <span>{{inviteData.tree_code}}</span>
                    <button class="copy" @click="copyInvite(inviteData.tree_code)">复制</button>
                </div>
                <div class="fr treepwd1">
                    <img :src="inviteData.register_qrcode"  alt="">
                    <p>加入财富自由之树</p>
                </div>
            </div>
        </div>

    </div>

</template>

<script>

    import treeBar from '@/page/tree/treeBar'
    import { treeInvite } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Clipboard from 'clipboard'

    export default {
        name: "Treeinvite",
        components: {
            treeBar
        },
        data()
        {
            return {
                tree_id:'',
                inviteData:{
                    user_code:'',
                    tree_code:'',
                    register_qrcode:''
                },
            }
        },
        created()
        {
            this.tree_id = this.$route.query.treeId
        },
        mounted()
        {
            this.getList()
        },
        methods:{

            // 我的财富树列表
            getList()
            {
                treeInvite({tree_id:this.tree_id}).then(res => {

                    this.inviteData = res
                })
            },

            // 复制邀请码
            copyInvite(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    Toast.success('复制成功');
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    Toast.fail('当前浏览器不支持');
                    // 释放内存
                    clipboard.destroy()
                })

            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/treeinvite.css";
</style>



